import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// react components for routing our app without refresh
import { Link } from "gatsby";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons
// core components
import Header from "components/Header/Header.jsx";
import Footer from "components/Footer/Footer.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import Button from "components/CustomButtons/Button.jsx";
import Parallax from "components/Parallax/Parallax.jsx";
import NavPills from "components/NavPills/NavPills.jsx";
// sections for this page
import HeaderLinks from "components/Header/HeaderLinks.jsx";
import SectionBasics from "./Sections/SectionBasics.jsx";
import SectionNavbars from "./Sections/SectionNavbars.jsx";
import SectionTabs from "./Sections/SectionTabs.jsx";
import SectionPills from "./Sections/SectionPills.jsx";
import SectionNotifications from "./Sections/SectionNotifications.jsx";
import SectionTypography from "./Sections/SectionTypography.jsx";
import SectionJavascript from "./Sections/SectionJavascript.jsx";
import SectionCarousel from "./Sections/SectionCarousel.jsx";
import SectionCompletedExamples from "./Sections/SectionCompletedExamples.jsx";
import SectionLogin from "./Sections/SectionLogin.jsx";
import SectionExamples from "./Sections/SectionExamples.jsx";
import SectionDownload from "./Sections/SectionDownload.jsx";
import bgimage from "assets/img/sign.jpg";
import componentsStyle from "assets/jss/material-kit-react/views/components.jsx";

class Components extends React.Component {
  render() {
    const { classes, ...rest } = this.props;
	const navImageClasses = classNames(classes.imgRounded, classes.imgGallery);
    return (
      <div>
        <Header
          brand="WhiteCat VFX Studio."
          rightLinks={<HeaderLinks />}
          fixed
          color="transparent"
          changeColorOnScroll={{
            height: 400,
            color: "white"
          }}
          {...rest}
        />
        <Parallax image={require("assets/img/bg4.jpg")}>
          <div className={classes.container}>
            <GridContainer>
              <GridItem>
                <div className={classes.brand}>
                  <h1 className={classes.title}>WhiteCat VFX Studio.</h1>
                  <h3 className={classes.subtitle}>
					Leading VFX Outsource Partner.
                  </h3>
                </div>
              </GridItem>
            </GridContainer>
          </div>
        </Parallax>

        <div className={classNames(classes.main, classes.mainRaised)} style={{backgroundImage: "url(" + bgimage + ")"}}>
		  <div className={classes.container}>
			  <GridContainer justify="center">
				<GridItem xs={12} sm={12} md={6}>				
					<div className={classes.profile}>
						<div className={classes.name}>
							<h2 style={{color:"black"}}>Here artistry intersects technology.</h2>                      
						</div>
					</div>
					<div style={{background: "white", padding: "20px", opacity: "0.75", borderRadius: "12px"}}>
						<h4 justify="center" style={{color:"black"}}>Founded in 2015, WhiteCat has provided VFX services on features such as Thuppaki, 
						Kaththi, Robo, 2.0, Sivaji, The Finest Hours, Pirates of the Caribbean: Dead Men Tell 
						No Tales, Alien Covenant, The Wrinkle In Time and TV productions and countless commercials.
						</h4>
						<h4 justify="center" style={{color:"black"}}>
						We've done VFX for notable Films and Series. Checkout our portfolio for more. No job is too big or too small. We look forward to working with you!
						</h4>
					</div>
				</GridItem>
			  </GridContainer>
		  			
			<SectionCarousel/>
			<div align="center">
				<Link to="/services">
					<Button>Checkout our portfolio.</Button>
				</Link>
			</div>
			<br />
			<br />
		  </div>

        </div>
        <Footer />
      </div>
    );
  }
}

export default withStyles(componentsStyle)(Components);
